/*
    Component: styleguide syntax
    ---
    Solarized Dark
    For use with Jekyll and Pygments
    http://ethanschoonover.com/solarized
*/

// SOLARIZED HEX      ROLE
// --------- -------- ------------------------------------------
// base01    #586e75  comments / secondary content
// base1     #93a1a1  body text / default code / primary content
// orange    #cb4b16  constants
// red       #dc322f  regex, special keywords
// blue      #268bd2  reserved keywords
// cyan      #2aa198  strings, numbers
// green     #859900  operators, other keywords

.highlighttable {
    @include padding-tb(0,0);
    @include margin-tb(.5,.5);
    display: block;
    overflow: auto;
    font-size: 90%;
    width: 100%;
    color: $alpha-text-color;
    border: $border-width $border-style $gamma-ui-color;
    border-radius: $radius;
}

    .linenos {
        @include padding-lr(.5,.5);
        color: $beta-text-color;
        background-color: lighten($gamma-ui-color,10);
        border-right: 1px solid $gamma-ui-color;
    }

    .code {
        @include padding-lr(1,0);
    }

.highlight .c { color: #586e75 } /* Comment */
.highlight .err { color: #93a1a1 } /* Error */
.highlight .g { color: #93a1a1 } /* Generic */
.highlight .k { color: #859900 } /* Keyword */
